$str-wid: 14.4;
$str-wid-m: 3.2;
.home {
  &__title {
    z-index: 3;
    opacity: 0;
    position: fixed;
    top: 15%;
    left: 50%;
    font-size: calc(50vw / $str-wid);
    font-weight: bold;
    letter-spacing: 12px;
    transform: translateX(-50%);
    animation: titlePopUp 1s 2s alternate forwards;
    white-space: nowrap;
  }
  &__subtitle {
    z-index: 3;
    opacity: 0;
    position: fixed;
    top: 25%;
    left: 50%;
    font-size: calc(32vw / $str-wid);
    font-weight: lighter;
    letter-spacing: 12px;
    transform: translateX(-50%);
    animation: titlePopUp 1s 3s alternate forwards;
  }
  &__logo {
    z-index: 10;
    cursor: pointer;
    position: fixed;
    top: 50px;
    right: 30px;
    width: calc(60vw / $str-wid);
    height: calc(60vw / $str-wid);
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__menu {
    z-index: 9;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 2s;
    width: calc(325vw / $str-wid);
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10vw 2vw 6vw 6vw;
    background-color: #fff7e7b3;
    a {
      font-size: calc(24vw / $str-wid);
      text-decoration: none;
      line-height: 3;
      color: #4e4c4c;
    }
  }
  &__downtown {
    z-index: 1;
    position: fixed;
    bottom: 0;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__house {
    z-index: 2;
    position: fixed;
    bottom: calc(80vw / $str-wid);
    left: calc(200vw / $str-wid);
    width: calc(324vw / $str-wid);
    height: calc(243vw / $str-wid);
    animation: popDown 1s 1s alternate forwards;
    &::before {
      position: absolute;
      content: "";
      display: block;
      top: calc(-106vw / $str-wid);
      left: calc(140vw / $str-wid);
      width: calc(281vw / $str-wid);
      height: calc(106vw / $str-wid);
      background: url(../../img/textbox-1.png) no-repeat center/100%;
      animation: popDown 1s 0.8s alternate forwards;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__house--next {
    z-index: 2;
    position: fixed;
    opacity: 0;
    bottom: 9.5vw;
    left: 40vw;
    width: calc(324vw / $str-wid);
    height: calc(243vw / $str-wid);
    animation: popUp 1s 1s alternate forwards;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__workers {
    opacity: 0;
    z-index: 3;
    position: fixed;
    bottom: calc(20vw / $str-wid);
    left: calc(500vw / $str-wid);
    width: calc(137vw / $str-wid);
    height: calc(180vw / $str-wid);
    animation: popUp 1s 0.3s alternate forwards;
    img {
      width: 100%;
      height: 100%;
    }
    &::before {
      opacity: 0;
      position: absolute;
      content: "";
      display: block;
      top: calc(-106vw / $str-wid);
      left: calc(-200vw / $str-wid);
      width: calc(281vw / $str-wid);
      height: calc(106vw / $str-wid);
      background: url(../../img/textbox-3.png) no-repeat center/100%;
      animation: popUp 1.5s 4s alternate forwards;
    }
  }
  &__bike {
    z-index: 3;
    position: fixed;
    bottom: calc(100vw / $str-wid);
    right: calc(300vw / $str-wid);
    width: clac(130vw / $str-wid);
    height: calc(132.35vw / $str-wid);
    transform: translateX(100%);
    animation: popLeft 3s 1s alternate forwards;
    img {
      width: 100%;
      height: 100%;
    }
    &::before {
      content: "";
      position: absolute;
      display: block;
      top: calc(-110vw / $str-wid);
      left: calc(50vw / $str-wid);
      width: calc(211vw / $str-wid);
      height: calc(106vw / $str-wid);
      background: url(../../img/textbox-2.png) no-repeat center/100%;
      animation: popDown 0.5s 1s alternate forwards;
    }
    &::after {
      opacity: 0;
      content: "";
      position: absolute;
      display: block;
      top: calc(-110vw / $str-wid);
      left: calc(50vw / $str-wid);
      width: calc(211vw / $str-wid);
      height: calc(106vw / $str-wid);
      background: url(../../img/textbox-4.png) no-repeat center/100%;
      animation: popUp 1s 5s alternate forwards;
    }
  }
}
@keyframes popUp {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}
@keyframes popDown {
  from {
    opacity: 1;
    transform: translateY(0%);
  }
  to {
    opacity: 0;
    transform: translateY(100%);
  }
}
@keyframes titlePopUp {
  from {
    opacity: 0;
    transform: translate(-50%, 100%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0%);
  }
}

@keyframes popLeft {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}

@media (max-width: 750px) {
  .home {
    &__title {
      top: 25%;
      font-size: calc(24vw / $str-wid-m);
    }
    &__subtitle {
      top: 35%;
      font-size: calc(14vw / $str-wid-m);
    }
    &__menu {
      width: calc(500vw / $str-wid-m);
      padding: 30vw 25vw 6vw 25vw;
      background-color: #fff7e7;
      transform: translateX(35%);
      a {
        font-size: calc(24vw / $str-wid-m);
      }
    }
    &__logo {
      top: 20px;
      width: calc(40vw / $str-wid-m);
      height: calc(40vw / $str-wid-m);
    }
    &__downtown {
      width: 200%;
      bottom: 20vw;
      left: -50%;
    }
    &__house {
      bottom: calc(80vw / $str-wid);
      left: calc(200vw / $str-wid);
      width: calc(160vw / $str-wid-m);
      height: calc(116vw / $str-wid-m);
      &::before {
        left: calc(300vw / $str-wid);
        bottom: calc(280vw / $str-wid);
        width: calc(281vw / $str-wid * 2);
        height: calc(106vw / $str-wid * 2);
      }
    }
    &__house--next {
      bottom: 38.5vw;
      left: 30vw;
      width: calc(160vw / $str-wid-m);
      height: calc(116vw / $str-wid-m);
    }
    &__workers {
      bottom: calc(80vw / $str-wid);
      left: calc(500vw / $str-wid);
      width: calc(81vw / $str-wid-m);
      height: calc(105vw / $str-wid-m);
      &::before {
        left: calc(-80vw / $str-wid-m);
        top: -15vw;
        width: calc(281vw / $str-wid * 2);
        height: calc(106vw / $str-wid * 2);
      }
    }
    &__bike {
      bottom: calc(300vw / $str-wid);
      right: calc(100vw / $str-wid);
      width: calc(77vw / $str-wid-m);
      height: calc(80vw / $str-wid-m);
      &::before {
        left: calc(300vw / $str-wid);
        bottom: calc(280vw / $str-wid);
        width: calc(281vw / $str-wid * 2);
        height: calc(106vw / $str-wid * 2);
      }
      &::after {
        left: -5vw;
        top: -20vw;
        width: calc(281vw / $str-wid * 2);
        height: calc(206vw / $str-wid * 2);
      }
    }
  }
}
