.menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: calc(100% - 10.01vh);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  pointer-events: none;
  opacity: 0;
  transition: all 0.5s;
  transform-origin: center;
  transform: translateY(100px);

  &--active {
    pointer-events: auto;
    opacity: 1;
    transform: translateY(0px);
  }

  &__container {
    background-color: rgba(23, 81, 87, 0.95);
    padding: 10px 30px;

    ul {
      li {
        position: relative;
        padding: 20px 30px;
        display: flex;
        justify-content: center;
        align-items: center;

        &::after {
          content: "";
          position: absolute;
          left: 0;
          bottom: 0;
          display: block;
          width: 100%;
          height: 1px;
          background: linear-gradient(to top, #d5a259, #ab7737);
        }

        &:last-child {
          &::after {
            display: none;
          }
        }

        a {
          font-family: $font-Sans;
          text-decoration: none;
          background: linear-gradient(to top, #ab7737, #f6cd7d);
          -webkit-background-clip: text;
          background-clip: text;
          color: transparent;
          font-weight: 600;
          font-size: $font-l;
          letter-spacing: 2px;
          transform-origin: center;
          transform: scaleX(0.9);
          @media (min-width: $M_devices) {
            font-size: 40px;
          }
        }
      }
    }
  }
}