.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  display: block;
  z-index: 999;
  max-width: 100vw;

  &::after {
    content: "";
    opacity: 0;
    width: 100%;
    height: 100%;
    transition: opacity .3s;
    left: 0;
    top: 0;
    z-index: -1;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  }

  &--active {
    &::after {
      content: "";
      position: absolute;
      opacity: 1;
      background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
    }
  }

  &__container {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 15px;

    @media (min-width: $M_devices) {
      padding: 20px 30px;
    }
  }

  &__menu {
    position: relative;
    width: 30px;
    height: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (min-width: $M_devices) {
      width: 40px;
      height: 40px;
    }

    span {
      position: relative;
      width: 100%;
      height: 2px;
      display: block;
      background-color: $menu-bar;
      transform-origin: left center;
      transition: all .3s;

      &:first-child {
        margin-bottom: 5px;
        @media (min-width: $M_devices) {
          margin-bottom: 10px;
        }
      }
    }

    &--active {
      span {
        &:nth-child(1) {
          transform: rotate(45deg);
          margin-bottom: 19px;
          @media (min-width: $M_devices) {
            margin-bottom: 26px;
          }
        }

        &:nth-child(2) {
          transform: rotate(-45deg);
        }
      }
    }
  }

  &__home {
    position: relative;
    width: 20%;
    display: block;

    @media (min-width: $M_devices) {
      width: 15%;
      max-width: 200px;
    }

    img {
      width: 100%;
    }
  }
}